// eslint-disable-next-line max-classes-per-file
import axios from 'axios';

export default class FeatureFlagApi {
  static async fetchAllFeatureFlags(username: string): Promise<any> {
    const environment = process.env.NODE_ENV;
    const request = `https://feature-flag-5iz3p3mbtq-de.a.run.app/feature-flag/list?email=${username}&environment=${environment}`;

    const result = await axios.get(request);

    const featureFlags = {} as any;

    result.data.forEach((item: {access: boolean; feature_flag_name: string}) => {
      featureFlags[item.feature_flag_name] = item.access;
    });

    return featureFlags;
  }
}
