<template>
  <div id="app">
    <navbar />
    <loading :active.sync="$store.state.isLoading" />
    <notifications position="bottom center" :ignore-duplicates="true" />
    <notifications v-if="debugMode" group="debug" position="bottom left" :duration="-1" />
    <router-view class="router-view" />
  </div>
</template>

<script>

import navbar from '@/components/navbar.vue';
import FeatureFlagApi from '@/apis/FeatureFlagApi';

export default {
  components: {
    navbar,
  },
  computed: {
    debugMode() {
      return this.$store.state.debugMode;
    },
  },
  async mounted() {
    // fetch all feature flags into store
    // expected to load feature flags once profile is ready
    try {
      if (this.$store.state.profile) {
        const featureFlags = await FeatureFlagApi.fetchAllFeatureFlags(this.$store.state.profile.username);
        this.$store.commit('updateFeatureFlags', featureFlags);
      }
    } catch (error) {
      console.error(error);
    }
  },
};
</script>

<style lang="scss">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  .router-view{
    padding-top: 50px;
  }
}
</style>
